
import PropTypes from "prop-types"
const propTypes = {
    /**
    * 
    * ** value : Array Object [{"a":"1", "b":"2", "c":"3"}]
    */
    dataSource: PropTypes.array.isRequired,
    /**
     * 
     ** value : Array Object [{"accessor":"", "Header":"", "sortable":true}]
    */
    columns: PropTypes.array.isRequired,
    /**
        * Primary Key
        ** value? : "ID"
    */
    dataKey: PropTypes.string.isRequired,
    /**
     * return style object 
     ** value? : (accessor, cellData, dataSource)=> {return {color:"red"}}
    */
    cellStyle: PropTypes.func,
    /**
     * return style object 
     ** value? : (rowdata)=> {return {color:"red"}}
    */
    rowStyle: PropTypes.func,
    /**
     *  selection
     ** value? : "radio" | "checkbox"
    */
    selection: PropTypes.string,
    /**
     *  filterable
     ** value? : true | false
    */
    filterable: PropTypes.bool,
    /**
        * function  Filter
        ** value? : (filterData) => {}
    */
    filterData: PropTypes.func,
    /**
     *  row number
     ** value? : true | false
    */
    rowNumber: PropTypes.bool,
    /**
     *  grid
     ** value? : 500
    */
   width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    /**
     * return style object
     ** value? : {color:"red"}
    */
    tableStyle: PropTypes.object,
    /**
     * return style object
     ** value? : {color:"red"}
    */
    headerStyle: PropTypes.object,
    /**
     * return style object  footer
     ** value? : (accessor, cellData, dataSource)=> {return {color:"red"}}
    */
    footerStyle: PropTypes.func,
    /**
     * style object group footer
     ** value? : {color:"red"}
    */
    groupFooterStyle: PropTypes.object,
    /**
     * return Array [Field]
     ** value? : ["ID", "Code"]
    */
    groupByData: PropTypes.array,
    /**
     * return Object  group  row
     ** value? : {"field":["ID"], "sumField":["Quantity"]}
    */
    groupBy: PropTypes.object,
    /**
     *  row 
     ** value? : 5
    */
    minRows: PropTypes.number,
    /**
     *  pagination
     ** value? : true | false
    */
    pagination: PropTypes.bool,
    /**
     * return 
     ** value? : (page) => {}
    */
    onPageChange: PropTypes.func,
    /**
     *  row 
     ** value? : 500
    */
    totalSize: PropTypes.number,
    /**
     * return 
     ** value? : (selectionValue) => {}
    */
    selectionData: PropTypes.func,
    /**
     *  selection 
     ** value? : [{ID:1},{ID:2},...]
    */
    selectionDefault: PropTypes.array,
    /**
     * 
     ** value? : true | false
    */
    clearSelectionChangePage: PropTypes.bool,
    /**
     * 
     ** value? : true | false
    */
    clearSelectionChangeData: PropTypes.bool,
    /**
     * 
     ** value? : true | false
    */
    clearSelectionAction: PropTypes.bool,
    /**
     * 
     ** value? : "100%" | 100
    */
    width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    /**
     * 
     ** value? : "100%" | 100
    */
    customTopControl:PropTypes.element,
    /**
     * 
     ** value? : "100%" | 100
    */
    customTopLeftControl:PropTypes.element,
    /**
     * 
     ** value? : "100%" | 100
    */
    customTopRightControl:PropTypes.element,
    /**
     * 
     ** value? : "100%" | 100
    */
    customBtmControl:PropTypes.element,
    /**
     * 
     ** value? : "100%" | 100
    */
    customBtmLeftControl:PropTypes.element,
    /**
     * 
     ** value? : "100%" | 100
    */
    customBtmRightControl:PropTypes.element,
    /**
     *  sort 
     ** value? : true | false
    */
    sortable:PropTypes.bool,
    /**
     * return 
     * value? : {"id": row.accessor,"sortDirection": SortDirection.ASC}
    */
   sortData:PropTypes.func,
   /**
    *  Selection Disabled
    ** value? : function return boolean
   */
    selectionDisabledCustom:PropTypes.func,
    /**
     * 
     ** value? : 50
    */
    pageSize:PropTypes.number,
    /**
     * 
     ** value? : (pageSize) => 
    */
    onPageSizeChange:PropTypes.func,
    /**
     * 
     ** value? : {margin:10, padding:10}
    */
    style:PropTypes.object,
    /**
     * reset  selection  true 
     ** value? : true | false
    */
    resetSelection:PropTypes.bool,
}

export default propTypes;